















































































































import Vue from "vue";
import ColorConvert from "@/util/colorConvert.ts";
import NaviPopup from "@/components/NaviPopup.vue"; // @ is an alias to /src

export default Vue.extend({
    components: {
        NaviPopup
    },
    data: function () {
        return {
            color: new ColorConvert(),
            hexT: "000000",
            setting: {
                mode: 0,
                modeOption: [
                    {label: "RGB", value: 0},
                    {label: "HSL", value: 1},
                    {label: "HSV", value: 2},
                ]
            },
        }
    },
    computed: {
        red: {
            get: function () {
                return this.$data.color.red;
            },
            set: function (val: number) {
                this.color.red = val;
            }
        },
        green: {
            get: function () {
                return this.$data.color.green;
            },
            set: function (val: number) {
                this.color.green = val;
            }
        },
        blue: {
            get: function () {
                return this.$data.color.blue;
            },
            set: function (val: number) {
                this.color.blue = val;
            }
        },
        hex: {
            get: function () {
                this.hexT = this.$data.color.hex;
                return this.$data.color.hex;
            },
            set: function (val: string) {
                this.color.hex = val;
            }
        },
        hue: {
            get: function() {
                return this.$data.color.hue;
            },
            set: function(val: number) {
                this.color.hue = val;
            }
        },
        sHSL: {
            get: function () {
                return this.$data.color.sHSL;
            },
            set: function (val: number) {
                this.color.sHSL = val;
            }
        },
        lHSL: {
            get: function () {
                return this.$data.color.lHSL;
            },
            set: function (val: number) {
                this.color.lHSL = val;
            }
        },
        sHSV: {
            get: function () {
                return this.$data.color.sHSV;
            },
            set: function (val: number) {
                this.color.sHSV = val;
            }
        },
        vHSV: {
            get: function () {
                return this.$data.color.vHSV;
            },
            set: function (val: number) {
                this.color.vHSV = val;
            }
        },
    },
    watch: {
        hexT(val) {
            if(/^[0-9a-f]{6}$/i.test(val)) {
                this.color.hex = val;
            }
        }
    }
})
